* {
    margin: 0;
    padding: 0;
    color: inherit;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    list-style: none;
    text-decoration: none;
  }

.header {
    padding: 2em;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
}

nav ul li {
    list-style: none;
    display: inline-block;
    padding: 1.3em;

}

nav ul li a {
    text-decoration: none;
}

.cartButton {
    align-self: center;
}

.header__logo {
    width: 75px;
}

.session__container {
    display: flex;
    flex-direction: row;
    gap: 2em;
}


.customCard {
    width: 350px;
    border: 1px solid red;
}


.MuiCard-root{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }